<template>
<div :style='{"width":"100%","padding":"30px calc((100% - 1200px)/2) 40px","margin":"0px auto","position":"relative","background":"#fff"}'>
    <el-form
	  :style='{"border":"1px solid #eee","padding":"30px 0","alignItems":"flex-start","flexWrap":"wrap","background":"#fff","display":"flex","width":"100%","position":"relative"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="120px"
    >
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="入库单号" prop="rukudanhao">
              <el-input v-model="ruleForm.rukudanhao" placeholder="入库单号" disabled></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="商品名称" prop="shangpinmingcheng">
            <el-input v-model="ruleForm.shangpinmingcheng" 
                placeholder="商品名称" clearable :disabled=" false  ||ro.shangpinmingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="商品分类" prop="shangpinfenlei">
            <el-input v-model="ruleForm.shangpinfenlei" 
                placeholder="商品分类" clearable :disabled=" false  ||ro.shangpinfenlei"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="进货数量" prop="alllimittimes">
            <el-input v-model.number="ruleForm.alllimittimes" 
                placeholder="进货数量" clearable :disabled=" false  ||ro.alllimittimes"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="入库价格" prop="rukujiage">
            <el-input v-model.number="ruleForm.rukujiage" 
                placeholder="入库价格" clearable :disabled=" false  ||ro.rukujiage"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="入库总价" prop="rukuzongjia">
              <el-input v-model="rukuzongjia" placeholder="入库总价" disabled></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="供应商" prop="gongyingshang">
            <el-input v-model="ruleForm.gongyingshang" 
                placeholder="供应商" clearable :disabled=" false  ||ro.gongyingshang"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="入库时间" prop="rukushijian">
              <el-date-picker
				  :disabled=" false  ||ro.rukushijian"
                  format="yyyy 年 MM 月 dd 日"
                  value-format="yyyy-MM-dd"
                  v-model="ruleForm.rukushijian" 
                  type="date"
                  placeholder="入库时间">
              </el-date-picker> 
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="员工工号" prop="yuangonggonghao">
            <el-input v-model="ruleForm.yuangonggonghao" 
                placeholder="员工工号" clearable :disabled=" false  ||ro.yuangonggonghao"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="员工姓名" prop="yuangongxingming">
            <el-input v-model="ruleForm.yuangongxingming" 
                placeholder="员工姓名" clearable :disabled=" false  ||ro.yuangongxingming"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="备注" prop="beizhu">
            <el-input v-model="ruleForm.beizhu" 
                placeholder="备注" clearable :disabled=" false  ||ro.beizhu"></el-input>
          </el-form-item>

      <el-form-item :style='{"width":"100%","padding":"0","margin":"0","textAlign":"center"}'>
        <el-button :style='{"border":"0px solid #eccc19","cursor":"pointer","padding":"0","margin":"0 20px 0 0","color":"#fff","borderRadius":"20px","background":"linear-gradient(to right,#f67536,#f64d36),#f64d36","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"1px solid #ccc","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#666","borderRadius":"20px","background":"none","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				rukudanhao : false,
				shangpinmingcheng : false,
				shangpinfenlei : false,
				alllimittimes : false,
				rukujiage : false,
				rukuzongjia : false,
				gongyingshang : false,
				rukushijian : false,
				yuangonggonghao : false,
				yuangongxingming : false,
				beizhu : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          rukudanhao: this.getUUID(),
          shangpinmingcheng: '',
          shangpinfenlei: '',
          alllimittimes: '',
          rukujiage: '',
          rukuzongjia: '',
          gongyingshang: '',
          rukushijian: '',
          yuangonggonghao: '',
          yuangongxingming: '',
          beizhu: '',
        },


        rules: {
          rukudanhao: [
          ],
          shangpinmingcheng: [
          ],
          shangpinfenlei: [
          ],
          alllimittimes: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          rukujiage: [
            { required: true, message: '入库价格不能为空', trigger: 'blur' },
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          rukuzongjia: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          gongyingshang: [
          ],
          rukushijian: [
          ],
          yuangonggonghao: [
          ],
          yuangongxingming: [
          ],
          beizhu: [
          ],
        },
		centerType: false,
      };
    },
    computed: {


        rukuzongjia:{
            get: function () {
                return 1*this.ruleForm.alllimittimes*this.ruleForm.rukujiage
            }
        },

    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.rukushijian = this.getCurDate()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='rukudanhao'){
              this.ruleForm.rukudanhao = obj[o];
              this.ro.rukudanhao = true;
              continue;
            }
            if(o=='shangpinmingcheng'){
              this.ruleForm.shangpinmingcheng = obj[o];
              this.ro.shangpinmingcheng = true;
              continue;
            }
            if(o=='shangpinfenlei'){
              this.ruleForm.shangpinfenlei = obj[o];
              this.ro.shangpinfenlei = true;
              continue;
            }
            if(o=='alllimittimes'){
              this.ruleForm.alllimittimes = obj[o];
              this.ro.alllimittimes = true;
              continue;
            }
            if(o=='rukujiage'){
              this.ruleForm.rukujiage = obj[o];
              this.ro.rukujiage = true;
              continue;
            }
            if(o=='rukuzongjia'){
              this.ruleForm.rukuzongjia = obj[o];
              this.ro.rukuzongjia = true;
              continue;
            }
            if(o=='gongyingshang'){
              this.ruleForm.gongyingshang = obj[o];
              this.ro.gongyingshang = true;
              continue;
            }
            if(o=='rukushijian'){
              this.ruleForm.rukushijian = obj[o];
              this.ro.rukushijian = true;
              continue;
            }
            if(o=='yuangonggonghao'){
              this.ruleForm.yuangonggonghao = obj[o];
              this.ro.yuangonggonghao = true;
              continue;
            }
            if(o=='yuangongxingming'){
              this.ruleForm.yuangongxingming = obj[o];
              this.ro.yuangongxingming = true;
              continue;
            }
            if(o=='beizhu'){
              this.ruleForm.beizhu = obj[o];
              this.ro.beizhu = true;
              continue;
            }
          }
            this.ruleForm.alllimittimes = 0;
            this.ro.alllimittimes = false;
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.yuangonggonghao!=''&&json.yuangonggonghao) || json.yuangonggonghao==0){
                this.ruleForm.yuangonggonghao = json.yuangonggonghao;
				this.ro.yuangonggonghao = true;
            }
            if((json.yuangongxingming!=''&&json.yuangongxingming) || json.yuangongxingming==0){
                this.ruleForm.yuangongxingming = json.yuangongxingming;
				this.ro.yuangongxingming = true;
            }
          }
        });

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`shangpinjinhuo/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {
			if(this.ruleForm.rukudanhao){
				this.ruleForm.rukudanhao = String(this.ruleForm.rukudanhao)
			}
			this.ruleForm.rukuzongjia = this.rukuzongjia
			if(!this.ruleForm.alllimittimes){
				this.$message.error("进货数量不能为空");
				return
			}
			var obj = JSON.parse(localStorage.getItem('crossObj'));
			var table = localStorage.getItem('crossTable');
			obj.alllimittimes = parseFloat(obj.alllimittimes) + parseFloat(this.ruleForm.alllimittimes)

			//this.$http.post(table+`/update`, obj).then(res => {});
			//更新跨表属性
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('shangpinjinhuo/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 跨表计算
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								var table = localStorage.getItem('crossTable');

								obj.alllimittimes = parseFloat(obj.alllimittimes) + parseFloat(this.ruleForm.alllimittimes)

								this.$http.post(table+`/update`,obj).then(res => {});
								this.$http.post(`shangpinjinhuo/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '操作成功',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {
						var obj = JSON.parse(localStorage.getItem('crossObj'));
						var table = localStorage.getItem('crossTable');

						obj.alllimittimes = parseFloat(obj.alllimittimes) + parseFloat(this.ruleForm.alllimittimes)

						this.$http.post(table+`/update`,obj).then(res => {});
						this.$http.post(`shangpinjinhuo/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// 获取uuid
		getUUID () {
			return new Date().getTime();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
    }
  };
</script>

